button {
  border: 1px solid #003BD1;
  border-radius: 10px;
  color: #003BD1;
  font-size: 16px;
  font-weight: 600;
  line-height: 20px;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.3s ease-in-out;
}
button:hover {
  background-color: #003BD1;
  color: #fff;
}