// Define your custom mixins here.
// -----------------------------------------------------------------------------

button {
  border: 1px solid $button-border-color;
  border-radius: 10px;
  color: $nav-link-color;
  font-size: 16px;
  font-weight: 600;
  line-height: 20px;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.3s ease-in-out;

  &:hover {
    background-color: $nav-link-color;
    color: $white;
  }
}
